  <div>
    <div class="login-header">
      <div class="header-warp">
        <span style="cursor: pointer;" @click="$router.push('/')"> 政企智慧服务平台</span><span >注册</span>
      </div>

    </div>
    <div class="register-body" style="position: relative">
      <div class="loginForm">
        <div class="fg-head" >
          注册
        </div>
        <div class="loginL">
          <ul>
            <li> <span style="color: #E84343">*</span> 用户名&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 密码&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 确认密码&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 邮箱号&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 验证码&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 邮箱验证码&nbsp;&nbsp;:&nbsp;</li>
          </ul>
        </div>
        <div class="loginR">
          <ul >
            <li>
<!--              <input type="text" placeholder="4-20位字符" style="padding-left: 5px" v-model="userName" @change="judUser">-->
              <el-input v-model="userName" placeholder="4-20位字符" @change="judUser" style="padding-left: 5px;width: 128%"></el-input>
            </li>
            <li>
<!--              <input type="password" placeholder="8-20位字符,至少包含大写字母、小写字母、数字、特殊符号中的两种" style="padding-left: 5px" ref="password"  v-model="password" >-->
              <el-input @change="regPassword" placeholder="8-20位字符,至少包含大写字母、小写字母、数字、特殊符号中的两种" style="padding-left: 5px;width: 128%" v-model="password" show-password></el-input>
<!--              <img src="../assets/eye.png" alt="" style="z-index: 999" @click="togglePassword">-->
            </li>
            <li>
<!--              <input type="password" v-model="repassword" @change="isEqual">-->
              <el-input placeholder="" v-model="repassword" show-password @change="isEqual" style="padding-left: 5px;width: 128%"></el-input>
            </li>
            <li>
<!--              <input type="text" v-model="mail" @change="regMain">-->
              <el-input v-model="mail" @change="regMain" style="padding-left: 5px;width: 128%" ></el-input>
            </li>
            <li class="verify">
              <el-input v-model="verify" style="padding-left: 5px;"></el-input>
              <img src="" alt="" @click="toggleVerify" ref="verify">
              <span @click="toggleVerify" style="cursor:pointer;">看不清楚？点击刷新</span>
            </li>
            <li class="phoneVerify">
              <el-input v-model="mailVerify" style="padding-left: 5px;"></el-input>
              <button id="verify" :class="{'verify1':verifyFlag}" :disabled="verifyFlag" @click="getMailVerify">获取验证码</button>
            </li>
            <li class="treaty">
              <el-checkbox v-model="checked">我已阅读并同意《用户注册协议》</el-checkbox>
            </li>
            <li class="register">
              <input type="button" value="注册" @click="toRegister">
            </li>
          </ul>
          <div style="margin-top: 13px">
            <span style="font-size: 14px;color: #AEAEAE">已有账户？</span> <span style="color: #008FFF;font-size: 14px;display: inline-block;margin-left: 19px;cursor: pointer" @click="$router.push('/login')">立即登录</span>
          </div>
        </div>
      </div>
    </div>
