      <div>版权所有:淮北市经信局</div>
      <div>网站备案号：皖ICP备10207486号 　网站标识码：3708000041</div>
      <div style="padding-bottom: 38px">党政机关网站标识号：CA162410000604238070001        皖公网安备 37089702000262号</div>
    </div>
