<script>
    export default {
        name: "register",
        data(){
            return{
                userName:'',
                password:'',
                repassword:'',
                mail:'',
                verify:'',
                mailVerify:'',
                checked:false,
                passwordSign:true,
                verifyFlag:false
            }
        },
        created(){

        },
        mounted(){
            this.toggleVerify()
        },
        methods:{
            //密码匹配
            regPassword(){
                var password = this.password;
                if (password.length<8){
                    this.$message({
                        message: '请输入8位以上密码',
                        type: 'warning'
                    });
                    this.password = '';
                    return false
                }
                if (password.length >20){
                    this.$message({
                        message: '请输入20位以上密码',
                        type: 'warning'
                    });
                    this.password = '';
                    return false
                }
            },
            //邮箱匹配
            regMain(){
                var strReg="";
                var r;
                var strText=this.mail;
                strReg=/^\w+((-\w+)|(\.\w+))*\@{1}\w+\.{1}\w{2,4}(\.{0,1}\w{2}){0,1}/ig;
                r=strText.search(strReg);
                console.log(r)
                if (r == -1){
                    this.$message({
                        message: '请输入合法的邮箱',
                        type: 'warning'
                    });
                    return false
                }
            },
            //密码显示隐藏
            togglePassword(){
                var password = this.$refs.password;
                if (this.passwordSign){
                    password.type="text";
                    this.passwordSign=false;
                }else  {
                    password.type="password";
                    this.passwordSign=true;
                }

                console.log(password)
            },
            toLogin() {
                this.$router.push('/login')
            },
            toggleVerify(){
                var that = this;
                var img = this.$refs.verify;
                img.src= 'http://192.168.30.27:8088/getCode?' +Math.random();
            },
            //用户名合法
            judUser(){
                var userName = this.userName;
                if (userName.length<4){
                    this.$message({
                        message: '请输入4位以上用户名',
                        type: 'warning'
                    });
                    return false
                }
                if (userName.length>20){
                    this.$message({
                        message: '请输入20位以下用户名',
                        type: 'warning'
                    });
                    return false
                }

            },
            //获取邮箱验证码
            getMailVerify(){
                // 检验验证码
                var that = this;
                var email = this.mail;
                let data = new FormData();
                data.append('code',this.verify);
                this.$axios.get( '/verificationCode?code='+that.verify)
                    .then(function(res) {

                    //邮箱验证码
                    if (res.data.code==0){
                        that.$axios.post('/user/sendCode', that.$qs.stringify({
                            email:email
                        })).then(function(res) {
                            if (res.data.code==0){
                                that.$message({
                                    message: '邮件已发发送!请检查邮箱',
                                    type: 'success'
                                });
                                that.verifyFlag = true;
                            }else {
                                that.$message({
                                    message: res.data.msg,
                                    type: 'warning'
                                });
                            }
                            console.log(res);
                        })
                            .catch(err=>{
                                that.$message({
                                    message: '网络错误',
                                    type: 'warning'
                                });
                            })
                    }else {
                        that.$message({
                            message: res.data.msg,
                            type: 'warning'
                        });
                        that.verify = '';
                        // that.toggleVerify();
                    }
                    console.log(res);
                })
            },
            //是否相等
            isEqual(){
                var password = this.password;
                var repassword = this.repassword;
                if (repassword.length<8){
                    this.$message({
                        message: '请输入8位以上密码',
                        type: 'warning'
                    });
                    this.repassword = '';
                    return false
                }
                if (repassword.length >20){
                    this.$message({
                        message: '请输入20位以上密码',
                        type: 'warning'
                    });
                    this.repassword = '';
                    return false
                }
                if (password != repassword){
                    this.$message({
                        message: '两次密码输入不一致',
                        type: 'warning'
                    });
                    this.repassword = '';
                    return false
                }
            },
            //注册
            toRegister(){
                var that = this;
                if (this.userName==''){
                    this.$message({
                        message: '请输入用户名',
                        type: 'warning'
                    });
                    return false
                }
                if (this.userName.length < 4){
                    this.$message({
                        message: '请输入4位以上用户名',
                        type: 'warning'
                    });
                    return false
                }
                if (this.userName.length > 20){
                    this.$message({
                        message: '请输入20位以下用户名',
                        type: 'warning'
                    });
                    return false
                }
                if (this.password==''){
                    this.$message({
                        message: '请输入密码',
                        type: 'warning'
                    });
                    return false
                }
                if (this.repassword==''){
                    this.$message({
                        message: '请输入重复密码',
                        type: 'warning'
                    });
                    return false
                }
                if (this.password != this.repassword){
                    this.$message({
                        message: '两次输入密码不相同',
                        type: 'warning'
                    });
                    return false
                }
                if (this.mail==''){
                    this.$message({
                        message: '请输入邮箱号',
                        type: 'warning'
                    });
                    return false
                }
                if (this.verify==''){
                    this.$message({
                        message: '请输入验证码',
                        type: 'warning'
                    });
                    return false
                }
                if (this.mailVerify==''){
                    this.$message({
                        message: '请输入邮箱验证码',
                        type: 'warning'
                    });
                    return false
                }
                if (!this.checked){
                    this.$message({
                        message: '请同意用户协议',
                        type: 'warning'
                    });
                    return false
                }
                that.$axios.post( '/user/checkCode', that.$qs.stringify({
                    code:this.mailVerify
                    })
                ).then(res=>{
                    console.log('邮箱号校验')
                    console.log(res)
                 that.verifyFlag = false;
                    if (res.data.code==0){
                        that.$axios.post( '/user/registerUser', that.$qs.stringify({
                            userName:that.userName,
                            password:that.password,
                            email:that.mail,
                            status:that.mailVerify
                        })).then(function(res) {
                            if (res.data.code==0){
                                that.$message({
                                    message: '账号已注册成功',
                                    type: 'success'
                                });
                                that.$router.push('/login')
                            }else {
                                that.$message.error(res.data.msg);
                                that.verify = '';
                            }
                            console.log(res);
                        })
                    }else {
                        this.$message({
                            message: '邮箱验证码不正确',
                            type: 'warning'
                        });
                    }
                })


            }
        },
        computed:{

        }
    }
</script>

